@import "../../scss/mixins";

.block-8 {
  background-image: url("../../assets/img/bg_live_sessions.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  .r1 {
    width: 100vw;
    text-align: center;
    .past-live {
      background-color: white;
      margin-top: 50px;
      h3 {
        background-image: linear-gradient(to right, #30c7df 0%, #f63caa 100%);
        padding: 10px 22px;
        border-radius: 5px;
        color: white;
        font-size: medium;
        max-width: 300px;
        margin: auto;
        transform: translateY(-20px);
      }
      ul {
        margin: 0px;
        padding: 0px;
        li {
          list-style: none;
          display: inline-block;
          margin: 5px;
          cursor: pointer;
        }
      }
      .btn-lm {
        padding: 10px 15px;
        border-radius: 5px;
        border: 1px solid black;
        background-color: transparent;
        outline: none;
        cursor: pointer;
        font-family: "Product-Sans-Bold";
        margin: 20px 0px;
      }
    }
    .upcoming {
      margin-top: 25px;
      .btn-upb {
        background-color: black;
        border-radius: 5px;
        padding: 12px 22px;
        outline: none;
        cursor: pointer;
        font-family: "Product-Sans-Bold";
        color: white;
        border: none;
        box-shadow: var(--elevation-2);
      }
      ul {
        margin: 0px;
        margin-top: 10px;
        margin-bottom: 40px;
        padding: 0px;
        li {
          width: 220px;
          height: 174px;
          display: inline-block;
          margin: 5px;
          background-size: contain;
          background-repeat: no-repeat;
          &:nth-child(1) {
            background-image: url("../../assets/img/us1.png");
          }
          &:nth-child(2) {
            background-image: url("../../assets/img/us2.png");
          }
          &:nth-child(3) {
            background-image: url("../../assets/img/us3.png");
          }
          &:nth-child(4) {
            background-image: url("../../assets/img/us4.png");
          }
          p {
            margin: 0px;
            margin-bottom: 2px;
            &.title {
              font-size: larger;
              font-family: "Product-Sans-Bold";
            }
            b {
              font-family: "Product-Sans-Bold";
            }
          }
        }
        .btn-rn {
          padding: 5px 10px;
          border-radius: 5px;
          border: 1px solid black;
          background-color: transparent;
          outline: none;
          cursor: pointer;
          font-family: "Product-Sans-Bold";
          font-size: 13px;
          margin-top: 5px;
        }
      }
    }
    h3 {
      font-family: "KGRedHands";
      color: #684fc0;
      font-size: 24px;
      max-width: 500px;
      margin: auto;
    }
    p {
      font-size: 16px;
      max-width: 450px;
      margin: auto;
      margin-top: 20px;
    }
    .btn-signup,
    .btn-login {
      color: black;
      font-family: "Montserrat-Bold";
      outline: none;
      border: none;
      border-radius: 500px;
      padding: 10px 22px;
      transition: all 0.51s var(--g-ease);
      background-color: white;
      cursor: pointer;
      &:hover {
        opacity: 0.7;
        background-color: #f5f5f5;
        box-shadow: var(--elevation-1);
      }
    }

    .btn-login {
      border: 1px solid black;
    }
  }
  .skill-list {
    width: 100%;
    text-align: center;
    ul {
      padding: 0px;
      margin: 0px;
      li {
        display: inline-block;
        width: 100px;
        height: 100px;
        margin: 10px 40px;
        list-style: none;
        img {
          width: 100%;
        }
      }
    }
  }

  .reviews {
    .item {
      width: 100%;
      height: 200px;
      margin-bottom: 10px;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      border-radius: 15px;
      box-shadow: var(--elevation-1);
      &.even {
        background-image: url("../../assets/img/bg_testi.png"),
          linear-gradient(to right, #ed5f76 0%, #f6b41d 100%);
      }
      &.odd {
        background-image: url("../../assets/img/bg_testi.png"),
          linear-gradient(to right, #21d0e3 0%, #ebd233 100%);
      }
      .parentTitle {
        height: 100%;
        .avatar {
          width: 100px;
          height: 100px;
          border-radius: 50px;
          box-shadow: var(--elevation-2);
          margin-right: -25px;
        }
        .content {
          padding-top: 25px;
          padding-right: 20px;
          padding-left: 15px;
          small {
            font-size: 10px;
          }
          h2,
          p {
            padding: 0px;
            margin: 0px;
          }
          h2 {
            margin-top: 10px;
          }
          p {
            font-size: 14px;
          }
        }
      }
    }
  }
}

@include for-phone-only {
  .block-8 {
    .r1 {
      background-image: none;
    }
  }
}
