@import "../../scss/mixins";

.block-4 {
  background-color: white;
  .r1 {
    height: 500px;
    background-size: contain;
    background-repeat: no-repeat;
    p {
      font-family: "KGRedHands";
      font-size: 24px;
      width: 200px;
      height: 200px;
      margin-left: 160px;
      margin-top: 200px;
    }
    &.block4Img {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 90%;
        margin-left: -30px;
        margin-top: 100px;
      }
    }
  }

  .r2 {
    h2 {
      font-family: "KGRedHands";
      font-size: 24px;
    }
    .content {
      h3 {
        font-size: 16px;
        font-family: "Product-Sans-Bold";
      }
      p {
        font-size: 14px;
      }
    }
    .img-circle {
      width: 50px;
      height: 50px;
      border-radius: 50px;
      box-shadow: var(--elevation-3);
      margin-top: 10px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}

@include for-phone-only {
  .block-4 {
    .r1.block4Img {
      height: 250px;
      img {
        margin-top: -80px;
      }
    }
    .r2 {
      .img-circle {
        width: 30px;
        height: 30px;
      }
    }
  }
}
