.categories {
  width: 100vw;
  h3 {
    font-family: "Product-Sans-Bold";
    font-size: 24px;
    text-align: center;
  }
  .category {
    width: 140px;
    height: 140px;
    text-align: center;
    background-color: white;
    padding-top: 25px;
    border-radius: 140px;
    overflow: hidden;
    cursor: pointer;
    transform: translateY(0);
    transition: all 0.51s var(--g-ease);
    box-shadow: none;
    &:hover {
      box-shadow: var(--elevation-2);
      transform: translateY(-5px);
      background-color: #fdfdfd;
    }
    .category-img {
      img {
        height: 50px;
      }
    }
    .category-label p {
      font-family: "KGRedHands";
      font-size: small;
    }
  }
}
