@import "../../scss/mixins";

.header {
  height: 100px;
  background-color: white;
  box-shadow: var(--elevation-1);
  width: 100%;
  transition: all 0.51s var(--g-ease);
  &.fixed-show {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
  }
  &.fixed-hide {
    position: fixed;
    top: -100px;
    left: 0;
    z-index: 1000;
  }
  img {
    height: 70px;
    width: auto;
  }
  .left,
  .rigth {
    width: 100px;
    display: inline-block;
  }

  .left {
    padding-left: 100px;
  }

  .rigth {
    width: 200px;
    float: right;
  }

  .btn-signup,
  .btn-login,
  .btn-subs {
    color: black;
    font-family: "Montserrat-Bold";
    outline: none;
    border: none;
    border-radius: 500px;
    padding: 10px 22px;
    transition: all 0.51s var(--g-ease);
    background-color: white;
    cursor: pointer;
    &:hover {
      opacity: 0.7;
      background-color: #f5f5f5;
      box-shadow: var(--elevation-1);
    }
  }

  .btn-login {
    border: 1px solid black;
  }
  .btn-subs {
    background-image: linear-gradient(to right, #ebd333 0%, #21d0e3 100%);
  }
}

@include for-phone-only {
  .header {
    .left {
      padding-left: 5px;
    }

    .rigth {
      width: 220px;
    }
    .btn-signup,
    .btn-login,
    .btn-subs {
      padding: 10px 10px;
    }
  }
}
