@import '../../scss/mixins';

.your-thing {
  background-image: url("../../assets/img/your_thing_bg.png");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  height: 500px;
  width: 100vw;
  position: relative;
  .whats {
    width: 400px;
    margin: auto;
    padding-top: 220px;
    max-width: 80vw;
    img {
      width: 100%;
      height: auto;
    }
  }

  .vid-btn {
    background-image: url("../../assets/img/yt_bg_image.jpeg");
    width: 300px;
    height: 300px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center center;
    border-radius: 300px;
    position: absolute;
    top: 0;
    left: 50px;
    z-index: 1;
    cursor: pointer;
    box-shadow: none;
    transform: translateY(0px);
    transition: all 0.51s var(--g-ease);
    &:hover {
      background-size: 120%;
      box-sizing: var(--elevation-2);
      transform: translateY(-5px);
    }
  }
}


@include for-phone-only{
  .your-thing{
    .vid-btn{
      width: 200px;
      height: 200px;
      left: calc(50vw - 100px);
    }

    .model{
      .model-content{
        width: 99vw;
        height: (99vw * 9) / 16;
      }
    }
  }
}