.block-5 {
  background-image: url("../../assets/img/bg_interactive.png");
  background-size: cover;
  background-repeat: no-repeat;
  h3 {
    font-family: "KGRedHands";
  }

  .card {
    position: relative;
    width: 100%;
    height: 250px;
    cursor: pointer;
    .badge {
      position: absolute;
      top: 20px;
      left: 20px;
      background-color: var(--yellow);
      width: 60px;
      height: 25px;
      border-radius: 25px;
      z-index: 2;
      box-shadow: var(--elevation-2);
      text-align: center;
      p {
        font-family: "KGRedHands";
        font-size: x-small;
        margin-top: 5px;
      }
    }
    .upper {
      width: 100%;
      height: 200px;
      border-radius: 15px;
      position: absolute;
      z-index: 1;
      box-shadow: var(--elevation-1);
      background-size: cover;
      background-position: center;
      h4,
      h5 {
        text-align: center;
        margin-top: 70px;
        color: white;
        font-size: 26px;
        font-family: "KGRedHands";
      }
      h5 {
        margin-top: -30px;
        color: var(--yellow);
      }
    }
    .lower {
      background-color: var(--yellow);
      width: 100%;
      height: 210px;
      border-radius: 15px;
      position: absolute;
      z-index: 0;
      transform: translateY(0);
      transition: all 0.51s var(--g-ease);
      box-shadow: var(--elevation-2);
      p {
        position: absolute;
        bottom: -10px;
        right: 10px;
      }
    }

    &:hover {
      .lower {
        transform: translateY(20px);
      }
    }
  }
}
