@import "../../scss/mixins";

.block-9 {
  width: 100vw;
  background-image: url("../../assets/img/footer_bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  text-align: center;
  padding-bottom: 300px;

  h3 {
    font-family: "KGRedHands";
  }

  .plans-list {
    ul {
      margin: 0px;
      padding: 0px;
      li {
        display: inline-block;
        list-style: none;
        margin: 10px;
        .card {
          width: 350px;
          max-width: calc(100vw - 40px);
          min-height: 300px;
          background-color: white;
          border-radius: 5px;
          overflow: hidden;
          box-shadow: var(--elevation-1);
          transition: all 0.51s var(--g-ease);
          &:hover {
            box-shadow: var(--elevation-2);
          }

          .title {
            width: 100%;
            height: 50px;
            p {
              line-height: 50px;
              font-family: "KGRedHands";
              text-align: center;
              color: white;
              margin: 0px;
            }
          }
          .content {
            margin-top: 25px;
            p {
              max-width: 300px;
            }
          }

          .btn-buy-sub {
            padding: 10px 35px;
            border-radius: 5px;
            color: white;
            background-color: #1a1a1a;
            outline: none;
            border: none;
            cursor: pointer;
            font-family: "KGRedHands";
            font-size: large;
            transition: all 0.51s var(--g-ease);
            &:hover {
              box-shadow: var(--elevation-2);
            }
          }
          &.live {
            .title {
              p {
                span {
                  width: 20px;
                  height: 20px;
                  background-color: #49ce20;
                  border-radius: 20px;
                  font-size: 9px;
                }
              }
            }
          }
        }

        &:nth-child(1) {
          width: 450px;
          max-width: 90vw;
          .card {
            width: 450px;
            max-width: 90vw;
            .title {
              background-image: linear-gradient(
                to right,
                #4bb4d8 0%,
                #dc4db0 100%
              );
            }
            .r1 {
              border-right: 1px solid rgba(128, 128, 128, 0.267);
            }
            .content {
              p {
                margin: auto;
                font-size: 14px;
                margin-bottom: 4px;
              }
              .p1 {
                width: 100px;
                font-weight: bolder;
                font-size: 24px;
                margin: auto;
                font-family: "Product-Sans-Bold";
              }
              .p2 {
                font-size: 20px;
                margin: 10px auto;
              }
              .strikethrough {
                position: relative;
              }
              .strikethrough:before {
                position: absolute;
                content: "";
                left: 0;
                top: 50%;
                right: 0;
                border-top: 3px solid;
                border-color: #ed3636;
                border-radius: 5px;
                -webkit-transform: rotate(-5deg);
                -moz-transform: rotate(-5deg);
                -ms-transform: rotate(-5deg);
                -o-transform: rotate(-5deg);
                transform: rotate(-5deg);
              }
            }
          }
        }

        &:nth-child(2) .card {
          .title {
            background-image: linear-gradient(
              to right,
              #f95190 0%,
              #f4b945 100%
            );
          }
          .content {
            margin-top: 0px;
            p {
              margin: auto;
              font-size: 14px;
              margin-bottom: 4px;
            }
            .p1 {
              width: 100px;
              font-weight: bolder;
              font-size: 24px;
              margin: auto;
              font-family: "Product-Sans-Bold";
            }
            .p2 {
              font-size: 20px;
              margin: 10px auto;
            }
            .strikethrough {
              position: relative;
            }
            .strikethrough:before {
              position: absolute;
              content: "";
              left: 0;
              top: 50%;
              right: 0;
              border-top: 3px solid;
              border-color: #ed3636;
              border-radius: 5px;
              -webkit-transform: rotate(-5deg);
              -moz-transform: rotate(-5deg);
              -ms-transform: rotate(-5deg);
              -o-transform: rotate(-5deg);
              transform: rotate(-5deg);
            }
          }
        }

        &:nth-child(3) .card {
          .title {
            background-image: linear-gradient(
              to right,
              #d4d347 0%,
              #38d0ce 100%
            );
          }
          .content {
            color: #224a81;
          }
        }

        &:nth-child(4) .card {
          .title {
            background-image: linear-gradient(
              to right,
              #cd55af 0%,
              #fe797b 100%
            );
          }
          .content {
            color: #228178;
          }
        }
      }
    }
  }
}
