@import "../../scss/mixins";

.block-6 {
  background-color: #f5f5f5;
  h3 {
    font-family: "KGRedHands";
  }
  .video-board {
    width: 100%;
    height: 400px;
    border-radius: 15px;
    background-image: linear-gradient(to right, #a894e1 0%, #7455cf 100%);

    .r1 {
      float: left;
      width: 35%;
      ul {
        margin-block-start: 0px;
        margin-block-end: 0px;
        padding: 0px;
        li {
          height: 35px;
          list-style: none;
          padding-left: 20px;
          cursor: pointer;
          p {
            color: white;
            line-height: 35px;
          }
          &:hover {
            background-image: linear-gradient(
              to right,
              rgba(0, 0, 0, 0.6) 0%,
              rgba(0, 0, 0, 0) 100%
            );
          }
        }
      }
    }

    .r2 {
      width: 65%;
      height: 100%;
      display: inline-block;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      overflow: hidden;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      transition: all 0.51s var(--g-ease);
      cursor: pointer;
    }
  }

  .card-list {
    position: relative;
    .title {
      width: 100%;
      height: 30px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      p {
        color: white;
        font-family: "KGRedHands";
        text-align: center;
        line-height: 30px;
      }
    }
    .col-lg-4:nth-child(1) .title {
      background-image: linear-gradient(to right, #4bb4d8 0%, #dc4db0 100%);
    }
    .col-lg-4:nth-child(2) .title {
      background-image: linear-gradient(to right, #f95190 0%, #f4b945 100%);
    }
    .col-lg-4:nth-child(3) .title {
      background-image: linear-gradient(to right, #d4d347 0%, #38d0ce 100%);
    }

    .content {
      width: 100%;
      height: 90px;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
      background-color: #efefef;
      z-index: 0;
      font-size: 14px;
    }
  }
}

@include for-phone-only {
  .block-6 {
    .video-board {
      .r1 {
        width: 100%;
      }
      .r2 {
        display: none;
      }
    }
  }
}
