.footer {
  background-color: #171621;
  width: 100vw;
  color: white;
  position: relative;
  h3 {
    font-weight: 900;
  }
  a {
    color: white;
    text-decoration: none;
  }

  ul {
    margin: 0px;
    padding: 0px;
    li {
      list-style: none;
      padding: 10px 0px;
    }
  }
  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 100%;
      max-width: 150px;
    }
  }
  .band{
    text-align: center;
  }
}
