@import "../../scss/mixins";

.banner {
  background-image: url("../../assets/img/bg_banner.png");
  background-size: cover;
  background-position: bottom center;
  width: 100vw;
  height: 650px;

  .caption-container {
    width: 350px;
    margin: auto;
    height: 500px;
    padding-top: 100px;
    padding-left: 0px;
    text-align: center;
    h3 {
      line-height: 30px;
      text-align: left;
      .st1 {
        color: #4bd1be;
        font-size: 35px;
        font-family: "Product-Sans-Regular";
        font-weight: 100;
        margin-left: 35px;
      }
      .st2 {
        color: #ebd333;
        font-size: 24px;
        font-family: "Product-Sans-Regular";
        font-weight: 100;
        margin-left: 50px;
      }
      .st3 {
        color: #ebd333;
        font-size: 40px;
        font-family: "Product-Sans-Regular";
        font-weight: 100;
      }
      .st4 {
        color: white;
        font-size: 35px;
        font-family: "Product-Sans-Bold";
        margin-left: 15px;
        &.t1 {
          margin-left: -10px !important;
        }
      }
      .st5 {
        color: #c5d254;
        font-size: 24px;
        font-family: "Product-Sans-Regular";
        font-weight: 100;
      }
      .st6 {
        color: #71d19d;
        font-size: 34px;
        font-family: "Product-Sans-Regular";
        margin-left: 100px;
        font-weight: 100;
      }
    }
    p.small {
      width: 250px;
      text-align: center;
      margin-left: 50px;
      color: white;
      font-size: 14px;
      font-family: "Product-Sans-Regular";
    }
  }
}

.btn-buy {
  padding: 10px 35px;
  border-radius: 500px;
  background-image: linear-gradient(to right, #ebd333 0%, #21d0e3 100%);
  outline: none;
  border: none;
  cursor: pointer;
  font-family: "Product-Sans-Bold";
  transition: all 0.51s var(--g-ease);
  font-size: large;
  &:hover {
    box-shadow: var(--elevation-2);
  }
}
